const publicState = {
  theme: 'light', // lingh亮， dark暗
}

const switchTheme = (state = publicState, action) => {
  switch (action.type) {
    case 'SWITCH_THEME':
      return { ...state, theme: action.value }
      break;
  
    default:
      return state
  }
  return state
}
export default switchTheme