import { Component } from 'react'
import { Routes, Route, NavLink } from 'react-router-dom'
import LeftNav from '@/Components/LeftNav'
import './index.scss'
import Notice from './Notice'
import Information from './Information'
import withRouter from '@/Libs/withRouter'

class News extends Component {
  constructor(props) {
    super(props)
    this.state = {
      navList: [
        { label: '平台公告', value: 'notice', link: '/index/news/notice' },
        { label: '相关资讯', value: 'information', link: '/index/news/information' }
      ]
    }
  }
  curClass = () => {
    if (this.props.location.pathname.includes('notice')) return 'notice'
    if (this.props.location.pathname.includes('information')) return 'information'
  }
  render() {
    return (
      <div className='w content_w'>
        <LeftNav />
        <div className="w_right">
          <p className='page_nav_tit'>资讯公告</p>
          <ul className='page_nav'>
            {
              this.state.navList.map(item => {
                return <li className={ this.curClass() === item.value ? 'page_nav_i cur' : 'page_nav_i' } key={ item.value }><NavLink to={ item.link }>{ item.label }</NavLink></li>
              })
            }
          </ul>
          <div className='news_content'>
            <Routes>
              <Route path="/notice" element={ <Notice /> } />
              <Route path="/information" element={ <Information /> } />
            </Routes>
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(News)