import { Component } from 'react'
import { Routes, Route } from 'react-router-dom'
import { Redirect } from '@/Libs/redirect.js'

import LayoutHead from './LayoutHead'
import LayoutFoo from './LayoutFoo'

import SportsBet from '@/Pages/SportsBet'
import ESports from '@/Pages/ESports'
import News from '@/Pages/News'
import InfomationDetail from '@/Pages/News/Information/Detail'
import Activity from '@/Pages/Activity'
import Help from '@/Pages/Help'
import Joint from '@/Pages/Joint'
import Personal from '@/Pages/Personal'

import './index.scss'

class Layout extends Component {
  constructor(props) {
    super(props)
    this.state = {
      visible: false
    }
  }
  render() {
    return (
      <div>
        <LayoutHead />
        <div className="public_content">
          <Routes>
            <Route exact path="/sportsbet" element={ <SportsBet /> } />
            <Route exact path="/esports" element={ <ESports /> } />
            {/* 新闻 */}
            <Route path="/news/*" element={ <News /> } />
            {/* 资讯详情 */}
            <Route path="/infomationDetail/:id" element={ <InfomationDetail /> } />
            {/* 优惠活动 */}
            <Route path="/activity/*" element={ <Activity /> } />
            {/* 帮助中心 */}
            <Route path="/help" element={ <Help /> } />
            {/* 合营中心 */}
            <Route path="/joint/*" element={ <Joint /> } />
            {/* 个人中心 */}
            <Route path="/personal/*" element={ <Personal /> } />
            <Route path='/personal' element={ <Redirect to="/index/personal/generalize" /> } />
          </Routes>
        </div>
        <LayoutFoo />
      </div>
    )
  }
}
export default Layout