import { Component } from 'react'
import { Table, Pagination } from 'antd';
import './index.scss'
class Task extends Component {
  constructor(props) {
    super(props)
    this.state = {
      listColumns: [
        { title: '任务内容', dataIndex: 'content', key: 'content' },
        { title: '任务类型', dataIndex: 'type', key: 'type' },
        { title: '任务奖励', dataIndex: 'reward', key: 'reward' },
        { title: '完成度', dataIndex: 'progress', key: 'progress' },
        { title: '任务状态', dataIndex: 'status', key: 'status', render: (text) => {
          return <span className='table_one_operation'>领取奖励</span>
        } }
      ],
      logColumns: [
        { title: '任务时间', dataIndex: 'content', key: 'content' },
        { title: '任务奖励', dataIndex: 'type', key: 'type' },
        { title: '任务内容', dataIndex: 'reward', key: 'reward' }
      ]
    }
  }
  render() {
    const data = [];
    for (let i = 0; i < 10; i++) {
      data.push({
        content: i,
        type: 'John Brown',
        progress: i + 1,
        reward: 'Lake Park',
        status: 'C',
      });
    }
    return (
      <div className='task_w'>
        <div className="task_left">
          <div className="task_tit"><p>任务列表</p><span>轻松赚取奖励和经验</span></div>
          <Table
            rowKey={'progress'}
            columns={ this.state.listColumns }
            dataSource={data}
            className="table_one table_head_one"
            pagination={ false }
            bordered
          />
          <div className='table_one_pagination'>
            <div className='table_one_pagination_info'>
              <p>共1页</p>
              <p>每页10条</p>
              <p>共8条记录</p>
            </div>
            <Pagination defaultCurrent={1} total={50} />
          </div>
        </div>
        <div className="task_right">
          <div className="task_tit"><p>任务日志</p><span>近期的10条日志，如有疑问，请联系在线客服</span></div>
          <Table
            rowKey={'progress'}
            columns={ this.state.logColumns }
            dataSource={data}
            className="table_one table_head_one"
            pagination={ false }
            bordered
          />
          <div className='table_one_pagination'>
            <div className='table_one_pagination_info'>
              <p>共1页</p>
              <p>每页10条</p>
              <p>共8条记录</p>
            </div>
            <Pagination defaultCurrent={1} total={50} />
          </div>
        </div>
      </div>
    )
  }
}
 export default Task