import { Component } from 'react'
import Swiper from 'swiper'
import 'swiper/swiper.scss'
import './index.scss'
import playList from './playList'

import arrow from '@/Assets/images/arrow.png'

class SwiperNav extends Component {
  constructor(props) {
    super(props)
    this.state = {
      swiperPlay: null,
    }
  }
  componentDidMount(){
    const templateSwiper = new Swiper ('.swiper-container', {
      slidesPerView: 'auto',
      spaceBetween: 10,
      loop: false, // 循环模式选项
      // 如果需要前进后退按钮
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
        hideOnClick: true,
      }
    })
    this.setState({ swiperPlay: templateSwiper })
  }
  render() {
    return (
      <div className='swiper_wrap swiper_nav'>
        <div className="swiper-container">
          <div className="swiper-wrapper">
            {
              playList.map((item, index) => {
                return (
                  <div key={ index } className="swiper-slide">
                    <div className='project_icon'>
                      <svg className='svg_comm project_icon_svg' width="1em" height="1em" viewBox="0 0 26 26" version="1.1" xmlns="http://www.w3.org/2000/svg">
                        <title>{ item.title }</title>
                        {
                          item.d && <path fill={ item.fill } d={ item.d } id={ item.title } />
                        }
                        {
                          item.ds && item.ds.map((item2, index2) => {
                            return <path key={ index2 } fill={ item.fill } d={ item2 } id={ item.title } />
                          }) 
                        }
                        
                      </svg>
                    </div>
                    <p className='project_tit'>{ item.title }</p>
                  </div>
                )
              })
            }
          </div>
          {/* <!-- 如果需要导航按钮 --> */}
          <div className="swiper-button-prev">
            <img className='swiper_arrow swiper_arrow_left' src={ arrow } alt="" />
          </div>
          <div className="swiper-button-next">
            <img className='swiper_arrow' src={ arrow } alt="" />
          </div>
        </div>
      </div>
    )
  }
}

export default SwiperNav