import { Component } from 'react'
import { NavLink } from 'react-router-dom'

class MinNav extends Component {
  render() {
    return (
      <ul className='min_nav'>
          {
            this.props.navList.map((item, index) => {
              return (
                <li key={ index }>
                  <NavLink to={`${item.link}`} className='min_nav_left'>
                    {
                      (() => {
                        if (item.icon) {
                          return <img src={ item.icon } alt={ item.title } />
                        } else {
                          return (
                            <svg className='svg_comm nav_svg' width="1em" height="1em" viewBox="0 0 17 17" version="1.1" xmlns="http://www.w3.org/2000/svg">
                              {
                                item.d && <path d={ item.d } id={ item.title } />
                              }
                              {
                                item.ds && item.ds.map((item2, index2) => {
                                  return <path key={ index2 } d={ item2 } id={ item.title } />
                                }) 
                              }
                            </svg>
                          )
                        }
                      })()
                    }
                    <p>{ item.title }</p>
                  </NavLink>
                </li>
              )
            })
          }
      </ul>
    )
  }
}

export default MinNav