import { Component } from 'react'
import { Routes, Route, NavLink } from 'react-router-dom'
import './index.scss'

import Month from './Month'
import Day from './Day'

class Statement extends Component {
  constructor(props) {
    super(props)
    this.state = {
      navList: [
        { label: '月结报表', value: 'month', link: '/index/joint/statement/month' },
        { label: '日结报表', value: 'day', link: '/index/joint/statement/day' },
        { label: '用户查询', value: 'client', link: '/' },
        { label: '账务查询', value: 'accounting', link: '/' },
        { label: '注单查询', value: 'order', link: '/' },
      ]
    }
  }
  render() {
    return (
      <div className='statement_w'>
        <ul className='statement_nav_list'>
          {
            this.state.navList.map(item => {
              return (
                <li key={ item.value }><NavLink to={ item.link }>{ item.label }</NavLink></li>
              )
            })
          }
        </ul>
        <div className='statement_content'>
          <Routes>
            <Route path="/month" element={ <Month /> }/>
            <Route path="/day" element={ <Day /> }/>
          </Routes>
        </div>
      </div>
    )
  }
}
export default Statement