import { Component } from 'react'
import { Table, Pagination } from 'antd';

import './index.scss'
class Information extends Component {
  constructor(props) {
    super(props)
    this.state = {
      listColumns: [
        // { title: '全选', dataIndex: 'content', key: 'content' },
        { title: '任务类型', dataIndex: 'type', key: 'type' },
        { title: '类型', dataIndex: 'reward', key: 'reward' },
        { title: '标题', dataIndex: 'progress', key: 'progress' },
        { title: '状态', dataIndex: 'status', key: 'status' }
      ],
      logColumns: [
        { title: '登录时间', dataIndex: 'content', key: 'content' },
        { title: 'IP地址', dataIndex: 'type', key: 'type' },
        { title: '归属地', dataIndex: 'reward', key: 'reward' }
      ],
      // 选中的key
      selectedRowKeys: []
    }
  }
  onSelectChange = selectedRowKeys => {
    console.log('选择的内容: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  }
  render() {
    const data = [];
    for (let i = 0; i < 10; i++) {
      data.push({
        content: i,
        type: 'John Brown',
        progress: i + 1,
        reward: 'Lake Park',
        status: 'C',
      });
    }
    const { selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      // columnTitle: '全选',
      onChange: this.onSelectChange,
      selections: [
        // 未读、已读
        {
          key: 'odd',
          text: '选择奇数行',
          onSelect: changableRowKeys => {
            let newSelectedRowKeys = [];
            newSelectedRowKeys = changableRowKeys.filter((key, index) => {
              console.log(key, 'kkk')
              if (index % 2 !== 0) {
                return false;
              }
              return true;
            });
            this.setState({ selectedRowKeys: newSelectedRowKeys });
          },
        },
        {
          key: 'even',
          text: '选择偶数行',
          onSelect: changableRowKeys => {
            let newSelectedRowKeys = [];
            newSelectedRowKeys = changableRowKeys.filter((key, index) => {
              if (index % 2 !== 0) {
                return true;
              }
              return false;
            });
            this.setState({ selectedRowKeys: newSelectedRowKeys });
          },
        },
      ],
    };
    return(
      <div className='information_w'>
        <div className="information_left">
          <div className="information_tit">
            <p className='information_tit_left'>消息列表</p>
            <div className="information_tit_operation">
              <span>全部标为已读</span>
              <span>批量删除</span>
            </div>
          </div>
          <Table
            rowSelection={rowSelection}
            rowKey={'progress'}
            columns={ this.state.listColumns }
            dataSource={data}
            className="table_one table_head_one"
            pagination={ false }
            bordered
          />
          <div className='table_one_pagination'>
            <div className='table_one_pagination_info'>
              <p>共1页</p>
              <p>每页10条</p>
              <p>共8条记录</p>
            </div>
            <Pagination defaultCurrent={1} total={50} />
          </div>
        </div>
        <div className="information_right">
          <div className="information_tit">
            <p className='information_tit_left'>登录日志<i>近期的10条日志，如有疑问，请联系在线客服</i></p>
          </div>
          <Table
            rowKey={'progress'}
            columns={ this.state.logColumns }
            dataSource={data}
            className="table_one table_head_one"
            pagination={ false }
            bordered
          />
          <div className='table_one_pagination'>
            <div className='table_one_pagination_info'>
              <p>共1页</p>
              <p>每页10条</p>
              <p>共8条记录</p>
            </div>
            <Pagination defaultCurrent={1} total={50} />
          </div>
        </div>
      </div>
    )
  }
}
export default Information