import { Component } from 'react'
import { Progress, Switch, Select } from 'antd'
import { connect } from 'react-redux'
import './index.scss'

import { playList, clubList, centreList, moreList } from './data'
import MinNav from './MinNav'
import { setToken, getToken } from '@/Libs/cookie.js'

import jackpotHeadImg from '@/Assets/images/sportsbet/jackpot1.png'
import vip1 from '@/Assets/images/vip1.png'
import light from '@/Assets/images/sportsbet/light.png'
import dark from '@/Assets/images/sportsbet/dark.png'

// 测试图片
import test1 from '@/Assets/images/test/test1.png'
import test2 from '@/Assets/images/test/test2.png'

class LeftNav extends Component {
  constructor(props) {
    super(props)
    this.state = {
      lanList: [
        { title: '中文', key: 'cn' },
        { title: '英文', key: 'en' },
      ],
      oddsList: [
        { label: '香港赔率', value: 'xg' },
        { label: '马来西亚', value: 'ml' },
        { label: '印尼赔率', value: 'yn' },
        { label: '欧洲赔率', value: 'oz' },
        { label: '美国赔率', value: 'mg' }
      ],
      defaultChecked: true
    }
  }
  componentDidMount() {
    if (getToken('OZ_THEME') === 'light' || !getToken('OZ_THEME')) {
      this.setState({ defaultChecked: true }, )
      this.clickSwitch(false)
      return
    } else {
      this.setState({ defaultChecked: false })
      this.clickSwitch(true)
    }
  }
  clickSwitch = (v) => {
    // v = true 代表浅色   v = false 代表深色
    console.log(v, 'vvvv')
    if (v) {
      // 暗
      document.getElementsByTagName('body')[0].classList.add('dark_theme')
      this.props.updateTheme('dark')
      setToken('OZ_THEME', 'dark')
      this.setState({ defaultChecked: true })
    } else {
      // 亮
      document.getElementsByTagName('body')[0].classList.remove('dark_theme')
      this.props.updateTheme('light')
      setToken('OZ_THEME', 'light')
      this.setState({ defaultChecked: false })
    }
  }
  render() {
    const { Option } = Select;
    return(
      <div className="w_left">
        {/* jackpot */}
        <div className="jackpot">
          <img className='jackpot_head_img' src={ jackpotHeadImg } alt="" />
          <p className='pond'><i>Ozbet  Lottery</i> 奖池</p>
          <p className='money'><i>8.88</i> USDT</p>
        </div>
        {/* 活动位一 */}
        <a className="activity_one" href="" target="_blank">
          <img src={ test1 } alt="" />
        </a>
        {/* 玩法列表 */}
        <MinNav navList={ playList }></MinNav>
        {/* 分割线 */}
        <div className='segment' />
        {/* 俱乐部 */}
        <MinNav navList={ clubList }></MinNav>
        {/* 等级奖励 */}
        <div className='grade'>
          <div className='grade_top'>
            <img src={ vip1 } alt="vip" />
            <p>下一次奖励</p>
          </div>
          <div className='grade_grogress'>
            <Progress percent={ 20 } showInfo={ false } strokeLinecap="round" strokeWidth="4px" strokeColor='#0385F2' trailColor='rgba(3, 133, 242, .1)' />
          </div>
          <p className='grade_grogress_num'>5000<i>/25000</i></p>
        </div>
        {/* 中心 */}
        <MinNav navList={ centreList }></MinNav>
        {/* 活动位二 */}
        <a className="activity_one" href="" target="_blank">
          <img src={ test2 } alt="" />
        </a>
        {/* 更多 */}
        <MinNav navList={ moreList }></MinNav>
        {/* 分割线 */}
        <div className='segment' />
        {/* 主题切换 */}
        <div className='theme'>
          <p className={ this.state.defaultChecked ? null : 'cur' }>浅色</p>
          <Switch
            onClick={ this.clickSwitch }
            checked={ this.state.defaultChecked }
            className='theme_switch'
            unCheckedChildren={ <img className='theme_switch_icon' src={ light } alt="浅色" /> }
            checkedChildren={ <img className='theme_switch_icon' src={ dark } alt="深色" /> }
          />
          <p className={ this.state.defaultChecked ? 'cur' : null }>深色</p>
        </div>
        {/* 语言or盘口 */}
        <div className='lan_or_odds'>
          <Select defaultValue="cn">
            {
              this.state.lanList.map(item => {
                return (
                  <Option key={ item.key } value={ item.key }>{ item.title }</Option>
                )
              })
            }
          </Select>
          <Select defaultValue="xg">
            {
              this.state.oddsList.map(item => {
                return (
                  <Option key={ item.value } value={ item.value }>{ item.label }</Option>
                )
              })
            }
          </Select>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  themeValue: state.PublicReducer.theme
})
const mapDispatchToProps =(dispatch) => {
  return {
    updateTheme: (v) => {
      dispatch({
        type: 'SWITCH_THEME',
        value: v
      })
    }
  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LeftNav)