import { BrowserRouter, Route, Routes } from 'react-router-dom'
import { Redirect } from '@/Libs/redirect.js'

import { publicRouters } from '@/Router/index.js'
import { Provider } from 'react-redux'
import Store from '@/Redux/store'

function App() {
  return (
    <Provider store={ Store }>
      <BrowserRouter>
        <Routes>
          {
            publicRouters.map(item => {
              return <Route exact key={ item.path } path={ item.path } element={ item.component } />
            })
          }
          <Route path='/' element={ <Redirect to="/index/sportsbet" /> } />
        </Routes>
      </BrowserRouter>
    </Provider>
  )
}
export default App;
