import { Component } from 'react'
import LeftNav from '@/Components/LeftNav'
import { Routes, Route, NavLink } from 'react-router-dom'
import withRouter from '@/Libs/withRouter'

import Count from './Count'
import Statement from './Statement'

class Joint extends Component {
  constructor(props) {
    super(props)
    this.state = {
      navList: [
        { label: '合营总览', value: 'count', link: '/index/joint/count' },
        { label: '数据报表', value: 'statement', link: '/index/joint/statement/month' }
      ]
    }
  }
  curClass = () => {
    if (this.props.location.pathname.includes('count')) return 'count'
    if (this.props.location.pathname.includes('statement')) return 'statement'
  }
  render() {
    return (
      <div className='w content_w'>
        <LeftNav />
        <div className="w_right">
          <p className='page_nav_tit'>合营中心</p>
          <ul className='page_nav'>
            {
              this.state.navList.map(item => {
                return <li className={ this.curClass() === item.value ? 'page_nav_i cur' : 'page_nav_i' } key={ item.value }><NavLink to={ item.link }>{ item.label }</NavLink></li>
              })
            }
          </ul>
          <div className='news_content'>
            <Routes>
              <Route path="/count" element={ <Count /> } />
              <Route path="/statement/*" element={ <Statement /> } />
            </Routes>
          </div>
        </div>
      </div>
    )
  }
}
export default withRouter(Joint)